<template>
	<div class="pay-main">
		<div class="pay-container">
			<div class="checkout-tit">
				<h4 class="tit-txt">
					<span class="success-icon"></span>
					<span class="success-info"
						>~~</span
					>
				</h4>
				<div class="paymark">
					<span class="fl"
						><em class="orange time">4</em
						><em>{{
							orderNum
						}}</em></span
					>
					<span class="fr"
						><em class="lead"></em
						><em class="orange money"
							>{{ payInfo.totalFee }}</em
						></span
					>
				</div>
			</div>
			<div class="checkout-info">
				<h4></h4>
				<ol>
					<li>
						<span class="zfb"></span
						>
					</li>
					<li></li>
					<li>
						
					</li>
				</ol>
				<h4>
					<span class="save"
						></span
					>
				</h4>
				<ul>
					<li>11111111</li>
					<li>111111</li>
					<li>111111</li>
				</ul>
			</div>
			<div class="checkout-steps">
				<div class="step-tit">
					<h5></h5>
				</div>
				<div class="step-cont">
					<ul class="payType">
						<li><img src="./images/pay2.jpg" /></li>
						<li><img src="./images/pay3.jpg" /></li>
					</ul>
				</div>
				<div class="hr"></div>

				<div class="payshipInfo">
					<div class="step-tit">
						<h5></h5>
					</div>
					<div class="step-cont">
						<ul class="payType">
							<li><img src="./images/pay10.jpg" /></li>
							<li><img src="./images/pay11.jpg" /></li>
							<li><img src="./images/pay12.jpg" /></li>
							<li><img src="./images/pay13.jpg" /></li>
							<li><img src="./images/pay14.jpg" /></li>
							<li><img src="./images/pay15.jpg" /></li>
							<li><img src="./images/pay16.jpg" /></li>
							<li><img src="./images/pay17.jpg" /></li>
							<li><img src="./images/pay18.jpg" /></li>
							<li><img src="./images/pay19.jpg" /></li>
							<li><img src="./images/pay20.jpg" /></li>
							<li><img src="./images/pay21.jpg" /></li>
							<li><img src="./images/pay22.jpg" /></li>
						</ul>
					</div>
				</div>
				<div class="hr"></div>

				<div class="submit">
					<!-- <router-link class="btn" to="/paysuccess"></router-link> -->
					<a href="javascript:;" class="btn" @click="pay"></a>
				</div>
				<div class="otherpay">
					<div class="step-tit">
						<h5></h5>
					</div>
					<div class="step-cont">
						<span
							><a href="weixinpay.html" target="_blank"
								></a
							></span
						>
						<span></span>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import QRCode from "qrcode";
export default {
	name: "Pay",
	data() {
		return {
			payInfo: {},
			orderNum: "",
			payStatus: 0,
		};
	},
	beforeMount() {
		this.orderNum = this.$route.query.orderNum;
	},
	mounted() {
		this.getPayInfo();
	},
	methods: {
		async getPayInfo() {
			const result = await this.$API.reqPayInfo(this.orderNum);
			if (result.code === 200) {
				this.payInfo = result.data;
			}
		},
		// 
		async pay() {
			// :codeUrl
			// With async/await
			// const generateQR = async (text) => {
			// 	try {
			// 		console.log(await QRCode.toDataURL(text));
			// 	} catch (err) {
			// 		console.error(err);
			// 	}
			// };

			try {
				// 
				let imgUrl = await QRCode.toDataURL(this.payInfo.codeUrl);
				//  this.$alertpromise. then,catch
				// thencatch: msgBox. ,,.
				this.$alert(`<img src="${imgUrl}" />`, "", {
					dangerouslyUseHTMLString: true,
					showClose: false,
					showCancelButton: true,
					cancelButtonText: "",
					confirmButtonText: "",
					center: true,
					// beforeClose,,
					beforeClose:(action, instance, done)=>{
						// action:: confirm- cancel- close-
						if(action==='confirm'){
							// 
							//if(!this.payStatus){
								// 
								// 1.
								//this.$message.info(', ');
								// 2.,
							//}
							// 
							done();
							clearInterval(this.timer);
							this.timer = null;
							this.$router.push('/paysuccess')
						}else if(action === 'cancel'){
							// 
							// this.$message.success 
							// info   warning  error
							// 1.
							this.$message.warning('..');
							// 2.
							clearInterval(this.timer); 
							this.timer = null;
							// 3.
							done(); //, 
						}
					}
				}).then(()=>{
					// 
				}).catch(()=>{
					//   console
				})


				//  x,.;
				// if,
				if (!this.timer) {
					this.timer = setInterval(async () => {
						const result = await this.$API.reqPayStatus(
							this.orderNum
						);
						if (result.code === 200) {
							//  
							//0.code,
							this.payStatus = 200;
							//1.
							this.$message.success("");
							//2. ++()
							clearInterval(this.timer); //clearInterval,
							this.timer = null; //id.clearTimeout, ,.
							//3.: MessageBox, 
							this.$$msgbox.close();
							this.$router.push("/paysuccess");
						} 
					}, 2000);
				}
			} catch (error) {
				// 
				this.$message.error("" + error.message);
			}
		},
	},
};
</script>

<style lang="less" scoped>
.pay-main {
	margin-bottom: 20px;

	.pay-container {
		margin: 0 auto;
